import React, {Component} from 'react'; 
import {graphql, Query} from 'react-apollo';
import gql from "graphql-tag"; //parses the query

import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import TranslateIcon from '@material-ui/icons/Translate';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import IconButton from '@material-ui/core/IconButton';

/*

1. Query the db, return all words for the language currently being studied on Duolingo.
2. Handle clicks on card: add to favourite, show individual translation.

*/


const langWordsQuery = gql`
    query langWords($lang: String!) { 
        langWords(lang: $lang){
            id
            lang
            flag
            lessonTitle
            singleWord
            transReturn
            favourite
        }
    }
`;

const updateFavouriteMutation = gql`
    mutation($id: ID!, $favourite: Boolean!){ 
        updateFavourite(id: $id, favourite: $favourite)
    }
`;


class GetWords extends Component {

    toggleSingleTranslation = (id) => {
        document.getElementById(id).classList.toggle('showTrans');

    }

    toggleFavourites = () => { //Needed to return to all view if no favourites; the link on page, not header
        document.getElementById("wordContainer").classList.toggle('showFavourites');
        return this.props.handleFavouritesClick();

    }

    updateFavourite = async word => { //Update the db record, and the local cache

        await this.props.updateFavourite({
            variables:{
                id: word.id,
                favourite: !word.favourite //do the opposite of the current value
            },
            
            update: store => { //store is Apollo's cache https://www.apollographql.com/docs/angular/features/cache-updates.html#directAccess //Don't need the response to update the cache, as it is just a boolean to check if it is a favourite or not.
                // Read the data from our cache for this query.
                const data = store.readQuery({query: langWordsQuery, variables: {lang: this.props.currentLang}}); //False is the default view, will generate onLoad

                data.langWords = data.langWords.map(x => x.id === word.id ? ({
                    ...word,
                    favourite: !word.favourite
                }) : x ) ; //loop through until it finds the correct one
                // Write our data back to the cache.
                store.writeQuery({ query: langWordsQuery, variables: {lang: this.props.currentLang}, data });
       
            }

        });
    };


    render(){

        //Query all words in the db.

        return (

            <Query query={langWordsQuery} variables={{lang: this.props.currentLang}}>

                {({loading, data, refetch}) => {

                    if(loading || this.props.newAdded === false){ //Are there new words in the process of being added? See App.js > getLessons()
                        return null;
                    }

                    const {langWords} = data;

                    if (langWords.every(e => e.favourite !== true) && this.props.favouritesView){ //If no favourites & favourites view is selected display help.
                        return (
                            <div className={"noFavouritesCont"}>
                                <Card className={"noFavourites"} onClick={this.toggleFavourites}>

                                    <CardContent className={"noFavouritesContent"}>
                                        <Typography variant="h5" component="h2">
                                            You currently have no favourites.<br />
                                            Add favourites by clicking
                                            <IconButton aria-label="Toggle Favourite Example" >
                                                <FavoriteBorderIcon />
                                            </IconButton>
                                        </Typography>
                                        <Typography component="p">
                                            <span className={"noFavouritesButton"}>
                                                Click here to return.
                                            </span>
                                        </Typography>
                                    </CardContent>

                                </Card>
                            </div>
                        )
                    }

                    else if(this.props.newAdded === true){ //All new words have been added. Refetch.
                        refetch();
                    }

                    return langWords.map(word => (

                        <Card key={word.id} className={"wordBoxCont"} data-flag={word.flag} data-favourite={word.favourite} data-lang={word.lang} data-lesson={word.lessonTitle}>

                            <CardContent className={"wordContent"}>
                                <Typography variant="h5" component="h2" className={"original"}>
                                    {word.singleWord}
                                </Typography>
                                <Typography component="p" id={word.id} className={"translation"}>
                                    {word.transReturn}
                                </Typography>
                            </CardContent>

                            <CardActions className={"buttons"}>

                                <IconButton aria-label="Toggle Favourite" className={"button favouriteButton"} size="small" onClick={() => {this.updateFavourite(word);}}>{/* Toggle if word is in Favourites list. Add a toggle disable until callback? */}

                                    {word.favourite === true ? <FavoriteIcon /> : <FavoriteBorderIcon />}

                                </IconButton>

                                <IconButton aria-label="Toggle Translation" className={"button translationButton"} size="small" onClick={() => this.toggleSingleTranslation(word.id)}>
                                    <TranslateIcon />
                                </IconButton>

                            </CardActions>

                        </Card>

                    ))

                }}   

            </Query>
        )
    }
}

//TO DO: Is it possible to export a named component with Apolloql?
export default graphql(updateFavouriteMutation, {name: 'updateFavourite'})(GetWords);