import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import * as serviceWorker from './serviceWorker';
import ApolloClient from "apollo-boost";
import {ApolloProvider} from 'react-apollo';

const client = new ApolloClient({
    //uri: "http://localhost:4000", //uri to Express server. "http://localhost:4000"
    uri: "https://duowordsdb.herokuapp.com", //uri to Express server.
    
});

//Wrapping the app in <ApolloProvider> allows Apollo to be accessed throughout the entire app.
ReactDOM.render(
    <ApolloProvider client={client}>
        <App />
    </ApolloProvider>,
    document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
