/*

    1a. Not all languages have the same ISO 639-1 & ISO 3166 code for languages and flags. Spain and Spanish does (es & es). Sweden and Swedish does not (se & sv)
    1b. This adds an ISO 3166 country code, so that a flag to represent the language can be rendered. NB These can be contentious, eg English represented by flag of USA, Portuguese flag represented by flag of Brasil.

    Available country codes:

    ["af","ax","al","dz","as","ad","ao","ai","aq","ag","ar","am","aw","au","at","az","bs","bh","bd","bb","by","be","bz","bj","bm","bt","bo","bq","ba","bw","bv","br","io","bn","bg","bf","bi","kh","cm","ca","cv","es-ct","ky","cf","td","cl","cn","cx","cc","co","km","cg","cd","ck","cr","ci","hr","cu","cw","cy","cz","dk","dj","dm","do","ec","eg","sv","gq","er","ee","et","eu","fk","fo","fj","fi","fr","gf","pf","tf","ga","gm","ge","de","gh","gi","gr","gl","gd","gp","gu","gt","gg","gn","gw","gy","ht","hm","va","hn","hk","hu","is","in","id","ir","iq","ie","im","il","it","jm","jp","je","jo","kz","ke","ki","kp","kr","kw","kg","la","lv","lb","ls","lr","ly","li","lt","lu","mo","mk","mg","mw","my","mv","ml","mt","mh","mq","mr","mu","yt","mx","fm","md","mc","mn","me","ms","ma","mz","mm","na","nr","np","nl","nc","nz","ni","ne","ng","nu","nf","mp","no","om","pk","pw","ps","pa","pg","py","pe","ph","pn","pl","pt","pr","qa","re","ro","ru","rw","bl","sh","kn","lc","mf","pm","vc","ws","sm","st","sa","sn","rs","sc","sl","sg","sx","sk","si","sb","so","za","gs","ss","es","lk","sd","sr","sj","sz","se","ch","sy","tw","tj","tz","th","tl","tg","tk","to","tt","tn","tr","tm","tc","tv","ug","ua","ae","gb","gb-eng","gb-nir","gb-sct","gb-wls","un","us","um","uy","uz","vu","ve","vn","vg","vi","wf","eh","ye","zm","zw"]

*/

export let checkFlag = (lang) => {

    switch (lang) {
        case "sv": //Swedish
            //console.log("Flag added for language - " + lang);
            return "se";

        //TO DO: Untested other than default from here!

        case "je": //Japanese
            //console.log("Flag added for language - " + lang);
            return "jp";

        case "kr": //Korean
            //console.log("Flag added for language - " + lang);
            return "ko";

        case "zh": //Chinese
            //console.log("Flag added for language - " + lang);
            return "cn";

        case "el": //Greek
            //console.log("Flag added for language - " + lang);
            return "gr";

        case "ga": //Irish
            //console.log("Flag added for language - " + lang);
            return "ie";

        case "he": //Hebrew
            //console.log("Flag added for language - " + lang);
            return "il";

        case "vi": //Vietnamese
            console.log("Flag added for language - " + lang);
            return "vn";

        case "da": //Danish
            //console.log("Flag added for language - " + lang);
            return "dk";

        case "hi": //Hindi
            //console.log("Flag added for language - " + lang);
            return "in";

        case "sw": //Swahili
            //console.log("Flag added for language - " + lang);
            return "tz";

        case "uk": //Ukrainian
            console.log("Flag added for language - " + lang);
            return "ua";

        case "cs": //Czech
            //console.log("Flag added for language - " + lang);
            return "cz";

        case "ar": //Arabic
            //console.log("Flag added for language - " + lang);
            return "ae"; //uae

        case "en": //English
            //console.log("Flag added for language - " + lang);
            return "gb"; //Not the USA

        case "cy": //Welsh
            //console.log("Flag added for language - " + lang);
            return "gb-wls"; //Wales

        case "gn": //Guarani
            //console.log("Flag added for language - " + lang);
            return "py"; //Paraguay

        case "pa": //Punjabi
            //console.log("Flag added for language - " + lang);
            return "in"; //India

        case "ca": //Catalan
            //console.log("Flag added for language - " + lang);
            return "es-ct"; //Catalan Flag

        //Leave Portuguese as Portugal, or Brasil?

        default: //TO DO: [EN] to Navajo, Hawaiian, Esperanto, High Valyrian, Klingon
            //console.log("Country code not changed for language code " + lang);
            return lang;
    }

}