import React, {Component} from 'react'; 
import "./styles/styles.scss";
import {graphql, compose} from 'react-apollo'; //Bind the query to the component. See export. //Rather than inject ever mutation here, use graphql's compose
import gql from "graphql-tag"; //parses the query

import CircularProgress from '@material-ui/core/CircularProgress';
import SimpleSnackbar from './snackbar';

import {Nav} from './Navigation';
import GetWords from './Words';

import {checkFlag} from './checkFlag';

//Return the favourites query as an object, or use ID and the cache will update automatically.

/*

Need to disable CORS in browser for this to work.

[getLessons > prepareWords > getTrans > createWord]
1. Call Duolingo API for latest lesson list.
2. Check db for each lesson title. If exists, skip.
3. If lesson title does not exit, find the longest word in each phrase in the lesson, translate it up using Duoling Translate (only accepts one word)
4. Add lessons that aren't already in the db.

5. Check a language has been set, a flag has been assigned, and no errors.
6. Query the db for all languages and flags for the navigation.
7. Query the db, return all words for the language currently being studied on Duolingo.


*/

/*

TO DO:
1. Improve initial loading of words from remote JSON. Loading screens. Pagination? Lazyload? Only one lesson at a time?
2. langsQuery & flag queries are imported twice. Find a better way.
3. Add ability to enter a username and pull other users lessons.
4. App.js getLessons() -> "this.lessonCheckQuery(lessonTitle, lang).then((value) => {" Don't make functions within loops!
5. Find alternative translation method. Number of API calls allowed?
6. Responsive design. CSS needs work.
7. Scroll top.
8. Error testing.


*/

//Variables in graphql are $variable
//Make sure the types as declared below match the ones in the schema.
const lessonCheckQuery = gql`
    mutation($lessonTitle: String!, $lang: String!){ 
        lessonCheck(lessonTitle: $lessonTitle, lang: $lang)
    }
`;

const createWordsMutation = gql`
    mutation($lang: String!, $flag: String! $lessonTitle: String!, $singleWord: String!, $transReturn:String){
        createWord(lang: $lang, flag: $flag, lessonTitle: $lessonTitle, singleWord: $singleWord, transReturn: $transReturn){
            id
            lang
            flag
            lessonTitle
            singleWord
            transReturn 
        }
    }
`;

const langsQuery = gql`{
    langs
}`;

class App extends Component {

    constructor(props) {
       
        super(props); //super allows "this"

        this.state = {
            currentLang: "", //This gets updated in lessonCheck, with fallback.
            currentFlag: "", //This gets updated in lessonCheck, with fallback.
            favouritesView: false, //toggleable favourites view
            noLoad: false, //if the language json object cannot be accessed, attempt to access the first language in the db, but display a loading error message.
            noLang: false, //Could not load json, and nothing stored in db
            noTrans: false //External translation error

        }

    }

    componentDidMount(){    

        this.getLessons();

    }//end componentDidMount

    componentDidUpdate(){

        if(this.props.langs.loading === false && this.state.noLoad === true && this.state.noLang === false && this.state.currentLang === ""){
            
            try { //The live version language json has failed to load, but there may be language info in the db; if there is, use it.

                if(typeof this.props.langs.langs[0] !== "undefined"){

                    let lang = this.props.langs.langs[0];

                    let flag = checkFlag(lang);

                    this.setState({
                        currentLang: lang,
                        currentFlag: flag,
                    })

                }

                else{

                    this.setState({ //Could not load json, and nothing stored in db.
                        noLang: true

                    })

                }

            }

            catch (error) {                
                console.log(error);
                
            }

        }

    }

    getLessons = () => {

        //fetch the data, wait for response "then()", then process
        //fetch('./resources/duolingoEmpty.json').then((response) => { //testing
        //fetch('./resources/duolingo.json').then((response) => { //testing
        //fetch('/duolingo/cduo.json').then(function(response) { //testing //Enable CORS in browser.
        fetch('https://www.duolingo.com/users/cduo').then(function(response) {  //Live version. Enable CORS in browser.

            // Convert to JSON
            return response.json();

        }).then((duoJ) => { // 'duoJ' is a JavaScript object

            let lang = Object.keys(duoJ.language_data).toString(); //get active language. JSON only returns the most recently accessed language. Change by visiting other language on the App/ website

            let flag = checkFlag(lang);

            this.setState({
                currentLang: lang,
                currentFlag: flag
            })

            for (let i = 0; i < duoJ.language_data[lang].skills.length; i++) {
            //for (var i = 0; i < 1; i++) { //testing to reduce API calls

                let lessonTitle = duoJ.language_data[lang].skills[i].title;
                let words = duoJ.language_data[lang].skills[i].words;

                //use lessonCheckQuery to check the lessonTitle. As this is returned by promise, have to use a then() to evaluate the value
                //TO DO: Don't make functions within loops! :/
                this.lessonCheckQuery(lessonTitle, lang).then((value) => {
                    //console.log(value.data.lessonCheck);

                    if(value.data.lessonCheck === true){
                        console.log("Lesson Exists in DB");
                    }
                    else{
                        console.log("To be added: Lesson " + lessonTitle);

                        this.setState({
                            newAdded: false
                        })

                        this.prepareWords(words, lang, flag, lessonTitle);

                    }

                })
                
            }

        }).catch((err) => {
            // Error :(
            console.log("Fetch Error");

            this.setState({
                noLoad: true
            })


        });
    }

    prepareWords = (words, lang, flag, lessonTitle) => {   

        var toTrans = [];

        for (var word in words) {
            
            //TO DO: Use alternate translation API? Only one word can be translated using Duolingo's dictionary, so check if there is more than one word. If there is, get the longest.
            var longestWord = words[word].split(' ').sort(function(a, b) { return b.length - a.length; });

            toTrans.push(longestWord[0]);
                   
        }

        this.getTrans(toTrans, lessonTitle, lang, flag);


    }//end prepareWords

    getTrans = (toTrans, lessonTitle, lang, flag) => {

        //Need to disable CORS in browser for this to work. Safari > Dev > Disable CORS
        var url = 'https://d2.duolingo.com/api/1/dictionary/hints/' + lang + '/en?tokens=' + encodeURIComponent('["' + toTrans.join('", "') + '"]');

        fetch(url).then((response) => { 

            // Convert to JSON
            return response.json();

        }).then((duoTransJ) => {

            for (var transWord in toTrans) {

                //check a word is returned
                var transCheck;
                if (duoTransJ[toTrans[transWord]].length) {
                    transCheck = duoTransJ[toTrans[transWord]].join(", ");
                }

                else{
                    transCheck = "[Not Found]";
                }

                this.createWord(lang, flag, lessonTitle, toTrans[transWord], transCheck); //send to db
                console.log("Adding: Lesson " + lessonTitle);

            }

            console.log("All added");

            this.setState({
                newAdded: true
            })

        }).catch((err) => {
            console.log("Translation Error");
            console.log(err);

            this.setState({
                noTrans: true
            })

        });

    }//end getTrans


    createWord = async (lang, flag, lessonTitle, singleWord, transReturn) => {
        //create Word
        await this.props.createWord({
                variables:{
                lang,
                flag,
                lessonTitle,
                singleWord,
                transReturn
            }
        });//No need to update cache, as this only gets called on page load.

    }

    lessonCheckQuery = async(lesson, language) => {

        //Feed in a lesson title, if exists, do not add.
        return await this.props.lessonCheck({
            variables:{
                lessonTitle: lesson,
                lang: language
            }

        })

    }

    handleFavouritesClick = () => {
        this.setState({
            favouritesView: !this.state.favouritesView

        });

    }

    changeLang = (lang) => {
        let newFlag = checkFlag(lang);

        //update the view without changing the value of the current DuoLingo language
        this.setState({
            currentLang: lang,
            currentFlag: newFlag,
            favouritesView: false

        });
        //Show all words, not just favourites
        document.getElementById("wordContainer").classList.remove('showFavourites');

    }

    displayError = (error, json) => { //Handle all snackbar error messages

        if(json === true){
            return <SimpleSnackbar message={error} timeout="10000" />
            
        }
        else{
            return (
                <div className={"loading"}>
                    <CircularProgress className={"loadingCircle"}/>
                    <SimpleSnackbar message={error} timeout="10000" />
                </div>
            )

        }

    }    

    render() {

        //Wait until the following have been generated.
        if((this.state.currentLang === "" || this.state.currentFlag === "" ) && this.state.noLang === false){
            return this.displayError("Attempting to update language information.")
        }
        else if(this.state.noTrans === true){
            return this.displayError("Translation error.")
        }
        else if(this.state.noLang === true){
            return this.displayError("No data found. Please check your internet connection and try again.")
        }

        return (

            <div id="app">

                <header>

                    <Nav newAdded={this.state.newAdded} changeLang={this.changeLang} handleFavouritesClick={this.handleFavouritesClick} toggleFavourites={this.toggleFavourites} currentLang={this.state.currentLang} />

                </header>

                <main role="main" id="wordContainer" className="words hideTrans">

                    <GetWords currentLang={this.state.currentLang} newAdded={this.state.newAdded} favouritesView={this.state.favouritesView} handleFavouritesClick={this.handleFavouritesClick} />

                    {/* If the language json fails to load, but there is language information in the db, display "Offline mode" */}
                    {this.state.noLoad ? this.displayError("Could not update language information. Offline mode activated.", true) : false}

                </main>

            </div>  

        );

    }
}

export default compose(
    graphql(createWordsMutation, {name: 'createWord'}), //name is how it will appear on this object.
    graphql(lessonCheckQuery, {name: 'lessonCheck'}),
    graphql(langsQuery, {name: 'langs'})
)(App); 

