import React, {Component} from 'react'; 
import {Query} from 'react-apollo';
import gql from "graphql-tag"; //parses the query

import TranslateIcon from '@material-ui/icons/Translate';
import FavoriteIcon from '@material-ui/icons/Favorite';
import Fab from '@material-ui/core/Fab';

import {checkFlag} from './checkFlag'; //?? Needed ?? Use the method on App.js

import FlagIconFactory from 'react-flag-icon-css'
const FlagIcon = FlagIconFactory(React, { useCssModules: false });

const langsQuery = gql`{
    langs
}`;

/*

1. Query the db for all languages and flags for the navigation.
2. Handle naviagation clicks for changing language, toggling favourites view, and translate all.

*/

export class Nav extends Component {

    constructor(props) {
       
        super(props);

        this.state = {
            favouritesView: false //initial view

        }

    }

    toggleAllTranslations = () => { //Toggle all translations 
        document.getElementById("wordContainer").classList.toggle('showTrans');

        //Hide all individual translations.
        //TO DO: Is there a better way? Feels inefficient for thousands of words... Add to/ remove from array?
        //Update cache? See updateFavourite
        //decide display on Render?
        //Actually faster to do this?

        let translations = document.getElementsByClassName("translation");
        for(var i=0; i<translations.length; i++) {
            translations[i].classList.remove('showTrans');
        }

    }

    toggleFavourites = () => { //Toggle Favourites View
        document.getElementById("wordContainer").classList.toggle('showFavourites');

        return this.props.handleFavouritesClick(); //pass to state on main App.


    }

    render(){

        return (

            <nav>

                <Query query={langsQuery}>  

                    {({loading, data, refetch}) => {
                        
                        if(loading){

                            return null;
                        }

                        const {langs} = data;

                        if(this.props.newAdded === true){ //All new words have been added. Refetch to get the lang & flag.
                            refetch();
                        }

                        return langs.map(lang => (

                            <Fab key={lang} className={"lang button"} onClick={() => {this.props.changeLang(lang)}} color="primary" aria-label="Change Language" size="small" data-current-language={this.props.currentLang === lang ? true : false}  >
                                <FlagIcon code={checkFlag(lang)} />
                            </Fab>
                        
                        ))}

                    }
                            

                </Query>

                <Fab color="primary" aria-label="Toggle Favourite" className={"button"} size="small" onClick={(e) => this.toggleFavourites()}>

                


                    <FavoriteIcon />
                </Fab>

                <Fab color="primary" aria-label="Toggle All Translations" className={"button"} size="small" onClick={(e) => this.toggleAllTranslations()}>
                    <TranslateIcon />
                </Fab>

            </nav>

        )
    }

}